.layout-button{
    width: 135px;
    text-align: center;
}

.layout-image-small{
    width: 62px;
    height: 48px;
    z-index: 99;

}

.layout-image-threeC{
    margin-top: 30px;
}
.example-enter {
    opacity: 0.01;
  }
  
  .example-enter.example-enter-active {
    opacity: 1;
    -webkit-animation: expand 0.5s ;
            animation: expand 0.5s ;
  }
  
  .example-leave {
    opacity: 1;
  }
  
  .example-leave.example-leave-active {
    opacity: 0.01;
    -webkit-animation: expandd 0.5s ;
            animation: expandd 0.5s ;
  }
  @-webkit-keyframes expand {
    from {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 0;
    }
  }
  @keyframes expand {
    from {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 0;
    }
  }
  @-webkit-keyframes expandd {
    from {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
  }
  @keyframes expandd {
    from {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
  }

  .preview-layout{
    width: 300px;
    height: 320px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.2);
    overflow-y: scroll;
  }
  .ant-slider {
    margin:  unset !important;
    padding: 9px 0 !important;
  }
  .preview-layout img {
    -webkit-animation: ease-in 2s;
            animation: ease-in 2s;
  }
  @-webkit-keyframes cf3FadeInOut {
    0% {
    opacity:1;
  }
  45% {
  opacity:1;
  }
  55% {
  opacity:0;
  }
  100% {
  opacity:0;
  }
}
  @keyframes cf3FadeInOut {
    0% {
    opacity:1;
  }
  45% {
  opacity:1;
  }
  55% {
  opacity:0;
  }
  100% {
  opacity:0;
  }
}
.item-drag-drop {
  position: relative;
  text-align: center;
  border: 1px dotted !important;
  margin-top: 20px;
/* animation: expand 0.5s; */

}

@-webkit-keyframes expand {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
}

@keyframes expand {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
}
.item-drag-drop:hover .handle {
  opacity: 1;
  background-color: #f7f7f7;

}
.item-drag-drop:hover .title-drag {
  opacity: 0.5;
}
.handle {
  position: absolute !important;
  width: 100% !important;
  height: 100% !important;
  top: 0 !important;
  left: 0 !important;
  display: -webkit-flex;
  display: flex;
  opacity: 1;
  -webkit-justify-content:flex-end;
          justify-content:flex-end;
  -webkit-align-items: center;
          align-items: center;
  color: red;
  transition: all ease-in-out 0.3s;
}
.fa-eye-slash,
.fa-eye {
  z-index: 99;
}
.title-drag {
  text-transform: uppercase;
  z-index: 99;
  font-size: 16px;
  letter-spacing: 0.2em;
  margin: 0 auto;
  font-weight: bold;
  opacity: 1;
}
.no-space{
  letter-spacing: unset !important;
}
.three-names{
  display: block;
  font-weight: bold;
}
.layout-image {
  width: 100%;
}

.image__wrap {
  position: relative;
  width: 170px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
}

.image__wrap:hover .img__description {
  visibility: visible;
  opacity: 1;
}
.img__description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  background: rgba(0, 0, 0, 0.72);
  color: #fff;
  visibility: hidden;
  opacity: 0;

  /* transition effect. not necessary */
  transition: opacity 0.2s, visibility 0.2s;
}
.ant-modal {
  width: 700px !important;
}
.layout-buttons .layouts-group {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  justify-items: center;
  grid-gap: 20px;
}
.layout-buttons .ant-radio-button-wrapper {
  height: unset !important;
  width: 200px;
  border-radius: unset !important;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.layout-buttons .ant-radio-button-wrapper-checked,
.layout-buttons .ant-radio-button-wrapper-checked:first-child {
  border: unset !important;
  border-radius: unset !important;
  box-shadow: 0px 0px 9px 0 #242222 !important;
}

